<template>
    <section class="HomePerspective__item">
        <div class="section__inner_default">
            <div class="graph-details">
                <div class="graph-details__counter">
                    <div class="graph-details__counter__result">
                        {{ totalValue }}
                    </div>

                    <div
                        class="graph-details__counter__efficiency"
                        :class="{ 'positive': positiveResult, 'negative': !positiveResult }">
                        {{ efficiencyValue }}
                    </div>
                </div>

                <div class="graph-details__title">
                    <h2>{{ nameGraph }}</h2>
                </div>
            </div>

            <div class="graph">
                <line-chart
                    :chart-data="data"
                    :height="200"
                    :options="options"></line-chart>
            </div>
        </div>
    </section>
</template>

<script>
import LineChart from './LineChart.vue'

export default {
    components: {
        LineChart
    },

    name: 'home-clients',

    data() {
        return {
            nameGraph: 'Clients',
            totalValue: 25,
            efficiencyValue: '+2',
            positiveResult: 1,
            data: {
                datasets: [{
                    data: [20, 25, 28, 33, 35, 36, 34],
                    backgroundColor: 'transparent',
                    borderColor: 'rgb(255, 255, 255)',
                    borderWidth: 10
                }],
                labels: ['jan', 'feb', 'mar', 'apr', 'may', 'jun', 'jul']
            },
            options: {
                legend: {
                    display: false
                },
                tooltips: {
                    enabled: false
                },
                scales: {
                    xAxes: [{
                        ticks: {
                            fontColor: 'rgb(193,200,225)',
                            fontFamily: 'Raleway, sans-serif',
                            fontSize: 40
                        },
                        gridLines: {
                            display: false,
                            color: 'rgba(255,255,255,0)'
                        }
                    }],
                    yAxes: [{
                        ticks: {
                            beginAtZero: true,
                            display: false
                        },
                        gridLines: {
                            display: false,
                            color: 'rgba(255,255,255,0)'
                        }
                    }]
                },
                hover: {
                    mode: null
                },
                elements: {
                    line: {
                        tension: 0
                    },
                    point: {
                        radius: 0
                    }
                },
                responsive: true,
                maintainAspectRatio: true
            }

        }
    }
}
</script>