<template>
  <div id="app">
    <home-hours-work />

    <div class="HomePerspective">
      <div class="HomePerspective__inner container">
        <home-clients />

        <home-gross-margin />
      </div>
    </div>

    <home-sales />

    <home-recruitment />
  </div>
</template>

<script>
import axios from 'axios'
import HomeHoursWork from './components/HomeHoursWork.vue'
import HomeClients from './components/HomeClients.vue'
import HomeGrossMargin from './components/HomeGrossMargin.vue'
import HomeSales from './components/HomeSales.vue'
import HomeRecruitment from './components/HomeRecruitment.vue'

export default {
  components: {
      HomeHoursWork,
      HomeClients,
      HomeGrossMargin,
      HomeSales,
      HomeRecruitment
  },

  name: 'app',

    data() {
        return {
            info: null
        };
    },

    mounted() {
        axios
            .get('https://api.coindesk.com/v1/bpi/currentprice.json')
            .then(response => (this.info = response.data.bpi))
            .catch(error => {console.log(error)});
    }
}
</script>

<style lang="scss">
.HomePerspective {
  margin-top: 44px;

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    width: calc(50% - 25px);
  }
}
</style>