        <div class="container">
            <div class="section__inner_default">
                <div class="graph-details">
                    <div class="graph-details__counter">
                        <div class="graph-details__counter__result">
                            {{ totalValue }} h
                        </div>

                        <div
                            class="graph-details__counter__efficiency"
                            :class="{ 'positive': positiveResult, 'negative': !positiveResult }">
                            {{ efficiencyValue }}%
                        </div>
                    </div>

                    <div class="graph-details__title">
                        <h2>{{ nameGraph }}</h2>
                    </div>
                </div>

                <div class="graph">
                    <bar-chart
                        :chart-data="data"
                        :height="100"
                        :options="options"></bar-chart>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import BarChart from './BarChart.vue'

export default {
    components: {
        BarChart
    },

    name: 'home-hours-work',

