                        ticks: {
                            fontColor: 'rgb(193,200,225)',
                            fontFamily: 'Raleway, sans-serif',
                            fontSize: 40
                        },
                        gridLines: {
                            display: false,
                            color: 'rgba(255,255,255,0)'
                        }
                    }],
                    yAxes: [{
                        ticks: {
                            beginAtZero: true,
